<template>
  <div class="video-card">
    <div class="cover-container">
      <div 
        v-if="!isPlaying"
        class="video-cover"
        :style="{ backgroundImage: `url(${props.video.cover})` }"
        @click="playCurrentVideo"
      >
        <!-- 移除播放按钮 -->
      </div>
      <iframe 
        v-else
        ref="iframeRef"
        :src="iframeUrl"
        frameborder="0"
        allow="autoplay; fullscreen; picture-in-picture"
        allowfullscreen
        class="video-iframe"
      ></iframe>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue';

const props = defineProps({
  video: {
    type: Object,
    required: true,
    validator: value => {
      return !!value.url.match(/bilibili\.com\/video\/(BV\w+)/);
    }
  }
});

// 全局播放状态管理
const activeVideoId = ref(null); // 当前正在播放的视频 ID
const isPlaying = ref(false);   // 当前视频是否正在播放
const iframeRef = ref(null);

// 计算当前视频的唯一标识符（可以使用 URL 或其他唯一字段）
const videoId = computed(() => props.video.url);

// 更健壮的 BV 号提取方式
const bvid = computed(() => {
  const match = props.video.url.match(/bilibili\.com\/video\/(BV\w+)/);
  return match ? match[1] : null;
});

// 自动提取分 P 参数
const page = computed(() => {
  const pageMatch = props.video.url.match(/[?&]p=(\d+)/);
  return pageMatch ? pageMatch[1] : 1;
});

// 构造 iframe URL
const iframeUrl = computed(() => {
  return `https://player.bilibili.com/player.html?
    bvid=${bvid.value}&
    page=${page.value}&
    high_quality=1&
    autoplay=0& <!-- 默认不自动播放 -->
    danmaku=0`.replace(/\s+/g, '');
});

// 播放当前视频
const playCurrentVideo = () => {
  if (activeVideoId.value && activeVideoId.value !== videoId.value) {
    // 如果有其他视频正在播放，则停止它
    stopOtherVideo();
  }

  if (!isPlaying.value) {
    isPlaying.value = true; // 开始播放
    activeVideoId.value = videoId.value; // 设置当前视频为活动视频
    setTimeout(() => {
      iframeRef.value?.contentWindow?.postMessage(
        { event: 'command', func: 'playVideo' },
        '*'
      );
    }, 500); // 确保 iframe 加载完成
  } else {
    isPlaying.value = false; // 停止播放
    activeVideoId.value = null; // 清空活动视频
    iframeRef.value?.contentWindow?.postMessage(
      { event: 'command', func: 'pauseVideo' },
      '*'
    );
  }
};

// 停止其他视频
const stopOtherVideo = () => {
  isPlaying.value = false; // 停止当前视频
  activeVideoId.value = null; // 清空活动视频
  iframeRef.value?.contentWindow?.postMessage(
    { event: 'command', func: 'pauseVideo' },
    '*'
  );
};

// 监听全局播放状态变化
watch(activeVideoId, (newId) => {
  if (newId !== videoId.value) {
    isPlaying.value = false; // 如果当前视频不是活动视频，则恢复封面状态
  }
});

// 监听消息实现自定义控制
onMounted(() => {
  window.addEventListener('message', (e) => {
    if (e.origin !== 'https://player.bilibili.com') return;
    if (e.data.event === 'pause') {
      isPlaying.value = false; // 如果 iframe 内部暂停，同步状态
      activeVideoId.value = null; // 清空活动视频
    }
  });
});
</script>

<style scoped>
/* 添加移动端优化 */
.video-card {
  display: block;
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.cover-container {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9 比例 */
  background: #1a1a1a;
  overflow: hidden; /* 防止内容溢出 */
}

.video-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover; /* 确保图片覆盖整个容器 */
  background-position: center; /* 居中显示 */
  background-repeat: no-repeat; /* 防止图片重复 */
  cursor: pointer;
  transition: opacity 0.3s;
}

.video-iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  background: #000;
  opacity: 0;
  transition: opacity 0.5s;
}

.video-iframe[src] {
  opacity: 1;
}
</style>